import { Component, OnInit, Input } from '@angular/core';
import { MatTab, MatTableDataSource, MatDialog } from '@angular/material';
import { Order } from '../model/Order';
import { AlertComponent } from '../alert/alert.component'; 
import { FormControl } from '@angular/forms';
import { OrderService } from '../service/order.service';
import { ActivatedRoute, Router } from '@angular/router';
import { User } from '../model/User';
import { UserService } from '../service/user.service';

@Component({
  selector: 'app-orders-table',
  templateUrl: './orders-table.component.html',
  styleUrls: ['./orders-table.component.scss']
})
export class OrdersTableComponent implements OnInit {

  private orderDataSource: MatTableDataSource<Order> = new MatTableDataSource<Order>(); 
  private $orders: Order[] = []; 
  private $selectedOrders: Order[] = []; 
  private selectAll: boolean = false; 
  private adjustedQuantity: FormControl = new FormControl(); 
  private $auth: User; 

  public displayedColumns = [
      'autorize', 
      'no',
      'drug',
      'quantity',
     // 'autorized',
      'ordered_quantity',
      'adjusted_quantity',
      'issued_quantity',   
      'expier_at',
      'is_recived',
      'recived_at']; 


  @Input() orders; 


  ngOnChanges(){
    this.$orders = this.orders;
    this.orderDataSource.data = this.orders;
    let temp = true; 
    this.$orders.forEach(
      (order) => {
        order.adjusted_quantity = order.ordered_quantity; 
        temp  = (order.autorized_by != null);
        if(temp){
          this.$selectedOrders.push(order);
        }
       
      }
    ) 
    this.selectAll = temp ;
    console.log(this.$selectedOrders); 
    console.log(this.$orders);
  }
  constructor(
    private _dialog: MatDialog, 
    private _order: OrderService, 
    private _activeRoute: ActivatedRoute, 
    private _route: Router, 
    private _user: UserService
  ) { }

  ngOnInit() {
    this._user.authUser().subscribe(
      (user) => {
        this.$auth = user; 
      }
    )
  }

  select(i, e){
    if(e.checked){
      this.$orders[i].autorized_by = this.$auth.id; 
      this.$selectedOrders.push(this.$orders[i]);
      
      if(this.$selectedOrders.length == this.$orders.length){
        this.selectAll = true; 
      }
    }
    else{
      this.$orders[i].autorized_by = null; 
      this.$selectedOrders.splice(this.$selectedOrders.indexOf(this.$orders[i]),1);
      this.selectAll = false; 
    }
    this.orderDataSource.data = this.orders;
  }

  _selectAll(e){
    this.selectAll = e.checked;
    if(e.checked){
      this.$orders.forEach(order => {
        order.autorized_by = this.$auth.id; 
      });
      this.$selectedOrders = this.$orders; 
    }else{
      this.$selectedOrders.forEach(order => {
        order.autorized_by = null; 
      });
      this.$selectedOrders = []; 
    }
    this.orderDataSource.data = this.orders;
  }

  autorize(){
    let dialogRef = this._dialog.open(AlertComponent, {
      width: "400px", 
      data: {
        message: "Are you shure you want to autorize the checked drugs", 
        dialog: "confirm"
      }
    }); 
    let orders = this.$selectedOrders; 
    dialogRef.afterClosed().subscribe(
      (response)=> {
        if(response.responce){
          this._order.autorize(this.$selectedOrders)
          .subscribe(
            (r) => {
              console.log("successs");
              this._route.navigate(["/drugs"]); 
            }
          )
        }
      }
    ); 
  }
}
